.container{
    background-color: #fff;
    width: 40rem;
    height: 20rem;
    position: relative;
    margin: 5rem;
    .pic{
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: rgb(65, 75, 75);
        z-index: 2;
        display: flex;
        justify-content: center;
        align-items: center;
        overflow: hidden;
        transition: .5s ease-in;
        &::before{
            content: attr(data-text);
            position: absolute;
            bottom: 0;
            left: 0;
            text-transform: uppercase;
            font-size: 6rem;
            font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
            font-weight: 700;
            letter-spacing: .3rem;
            text-align: center;
            color: gray;
            // z-index: 3;
            width: 100%;
            // height: 100%;
        }
        img{
            max-width:12rem;
            height: 7rem;
            // position: relative;
            transition: all .5s ease-in;
            z-index: 3;
           
        }
        
    }
    &:hover .pic{
        width: 17rem;
        height:10rem ;
        top: calc(50% - 5rem);
        left: -5rem;
        background-color: #ff0057;
        transition: .5s ease-in;
        img{
            max-width: 7.6rem;
            height: 4rem;
            transition: all .5s ease-in;
            
        }
    }
    // styling the content
    .content{
        display: flex;
        flex-direction: column;
        position: absolute;
        top: 3rem;
        left: 15rem;
        @mixin text-style(){
            color: #3a3a3a;
            text-transform: capitalize;
            font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
            padding-bottom: 2rem;
        }
        h3{
            @include text-style();
            font-size: 2rem;
            font-weight: 500;
            position: relative;
            &::after{
                content: "";
                position: absolute;
                width: 9rem;
                height: 5px;
                border-radius: 10rem;
                background-color: #8871cd;
                bottom: 1.7rem;
                left: -1rem;


            }
            &::before{
                content: "";
                position: absolute;
                width: 1rem;
                height: 5px;
                border-radius: 10rem;
                background-color: red;
                z-index: 1;
                bottom: 1.7rem;
                left: -1rem;
                animation: line_move 1s infinite alternate;
            }
            @keyframes line_move {
                0%{
                    left: -1;
                }
                100%{
                    left: 7rem
                }
            }

        }
        p{
            @include text-style();
            font-size: 1.3rem;
        }
        a{
            text-decoration: none;
            background-color: rgb(94, 60, 228);
            width: fit-content;
            // @include text-style();
            padding: 1rem;
            border-radius: .75rem;
            text-transform: uppercase;
            font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
            font-weight: 500;
            color: #fff;
            position: relative;
            z-index: 1;
            &::after{
                content: "";
                position: absolute;
                width: 100%;
                height: 100%;
                background-color: #8871cd;
                top: 0;
                left: 0;
                border-radius: .75rem;
                transform-origin: left;
                transform: scaleX(0);
                transition: transform .5s ease-in;
                z-index: -1;
            }
            &:hover::after{
                transform: scaleX(1);
                transform-origin: right;
            }
        }

    }

}